import React from "react"
import { graphql } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import { Translation } from "react-i18next"
import i18n from "../../lib/i18n"

import HeroCareers from "../../components/heros/hero-careers"
import CareerCards from "../../components/careers/career-cards" 
export const query = graphql`
  query {
    wpHero(id: { eq: "cG9zdDoxMjM2" }) {
      id
      title
      heroFields {
        en {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
        es {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
      }
    }
  }
`

const Careers = ({ data }) => {
  const hero = data.wpHero.heroFields
  return (
    <Layout>
      <Translation>
        {t => (
          <>
            {i18n.language === "en" ? (
              <SEO title="Texas HVAC Company | Residential &amp; Commercial HVAC Contractors" />
            ) : (
              <SEO title="Compañía de HVAC en Texas | Contratadores de HVAC Residencial y Comercial" />
            )}
            <HeroCareers
              headline={`${hero[i18n.language].headline}`}
              subheadline={`${hero[i18n.language].subheadline}`}
              body={`${hero[i18n.language].body}`}
              image={`${hero[i18n.language].image.sourceUrl}`}
              button2Text={`${hero[i18n.language].buttontext}`}
              button2Link={
                hero[i18n.language].buttonlink !== null
                  ? `${hero[i18n.language].buttonlink}`
                  : `#getInTouch`
              }
            />
          </>
        )}
      </Translation>
      <CareerCards />
    </Layout>
  )
}

export default Careers
